.ui.container.main {
  position: relative;
}
.ui.container.main .ui.grid {
  height: 100%;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
.ui.container.main .ui.grid .column {
  display: flex !important;
  justify-content: center;
}
.seven.wide.column {
  z-index: 1;
  margin: 100px 0;
}
.nine.wide.column {
  z-index: 0;
}
.left-side-wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    'avatar dialog'
    'actions actions';
  height: 472px;
}
div#avatar-container {
  position: relative;
  width: 128px;
  height: 320px;
  padding-top: 80px;
  grid-area: avatar;
}
div#avatar-container .avatar-shadow {
  box-shadow: var(--character-shadow);
  width: 56%;
  height: 1px;
  border-radius: 50%;
  margin: -26px auto 0;
}
div#dialog-box-container {
  width: 100%;
  height: 320px;
  grid-area: dialog;
  padding-top: 40px;
}
div#dialog-box {
  position: relative;
  display: flex;
  align-items: center;
  background-color: var(--transparent-white);
  width: calc(100% - 8px);
  height: auto;
  min-height: 100px;
  max-height: 100%;
  padding: 20px 30px 24px 30px;
  margin: 4px;
  filter: drop-shadow(-2px 6px 6px var(--world-shadow-color));
}
.night div#dialog-box {
  background-color: var(--transparent-black);
  border-radius: 4px;
}
div#dialog-box::before {
  content: '';
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid var(--transparent-white);
  border-top: 10px solid transparent;
  border-bottom: 10px solid var(--transparent-white);
  left: -24px;
  top: 60px;
}
.night div#dialog-box::before {
  border-right: 10px solid var(--transparent-black);
  border-bottom: 10px solid var(--transparent-black);
  left: -20px;
}
div#dialog-box p {
  font-family: 'VG';
  font-size: 12px;
  line-height: 22px;
  display: inline;
}
div#actions-wrapper {
  display: grid;
  grid-template-columns: 2fr 5fr;
  width: 100%;
  filter: drop-shadow(-2px 6px 6px var(--world-shadow-color));
  grid-area: actions;
}
div#action-suggestion {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  background-color: var(--transparent-white);
  z-index: 1;
}
.night div#action-suggestion {
  background-color: var(--transparent-black);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
div#action-suggestion p {
  color: #333;
  font-size: 10px;
  text-align: left;
  text-transform: uppercase;
}
.night div#action-suggestion p {
  color: #bbb;
}
div#actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: var(--transparent-white);
  overflow: hidden;
}
.night div#actions {
  background-color: var(--transparent-black);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
button.visitor-action {
  position: relative;
  width: 100%;
  height: 36px;
  text-align: left;
  font-family: 'VG';
  font-size: 12px;
  padding-left: 20px;
  background-color: transparent;
  border: none;
}
button.visitor-action:focus {
  background-color: #c0e0e0;
  color: #000;
  border: none;
  outline: 0;
}
button.visitor-action:hover {
  background-color: #a3c7c7;
  color: #000;
}
button.visitor-action::before {
  content: '-';
  position: absolute;
  left: 0px;
  top: 1px;
  height: 30px;
  color: #000;
  font-size: 16px;
  line-height: 34px;
}
.night button.visitor-action::before {
  color: #bbb;
}
button.visitor-action:hover::before {
  content: '>';
  position: absolute;
  left: 0px;
  top: 1px;
  height: 16px;
  color: #000;
  font-size: 16px;
  line-height: 34px;
  opacity: 1;
  animation: blink 0.5s linear 0.1s infinite;
}
.ui.button,
.ui.buttons .button,
.ui.buttons .or {
  font-size: 0.8rem;
  margin-top: 10px;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
div#dialog-box,
div#actions-wrapper {
  border-style: solid;
  border-width: 4px;
  border-image-slice: 2;
  border-image-width: 2;
  border-image-repeat: stretch;
  border-image-outset: 1;
  background-clip: padding-box;
}
.night div#dialog-box,
.night div#actions-wrapper {
  border: none;
}
.right-side-wrapper {
  width: 80%;
  position: relative;
}
.right-side-wrapper h1,
.right-side-wrapper h2,
.right-side-wrapper h3 {
  margin: 15px 0;
  overflow-wrap: anywhere;
}
div#sign-shadow {
  width: 100%;
  height: 40%;
  position: absolute;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  bottom: 74px;
  left: -108px;
  transform: rotate(170deg) skew(30deg, 10deg);
  filter: blur(2px);
}
div#sign {
  min-height: 650px;
  padding: 30px 30px 24px 30px;
  background-color: white;
  filter: drop-shadow(-2px 6px 6px var(--world-shadow-color));
  border-radius: 18px;
  z-index: 2;
  position: relative;
}
.night div#sign {
  background-blend-mode: luminosity;
}
.night div#sign-base {
  filter: grayscale(1);
}
div#sign > div {
  color: #000;
  margin: 20px 0px;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: white;
}
.night div#sign > div {
  padding-top: 10px;
  background-color: var(--transparent-black);
  border-radius: 4px;
}
.night div#sign > div::before,
.night div#sign > div::after {
  background-image: none;
}
div#sign div#loader {
  background-color: transparent !important;
  width: 86% !important;
  height: 120px !important;
}
div#sign div#loader i {
  color: #444;
}
div#loader::before,
div#loader::after {
  background-image: none !important;
}
div#sign > div p {
  font-size: 9px;
  color: #555;
}
.night div#sign > div p {
  color: #ccc;
}
div#sign > div::before {
  content: ' ';
  display: block;
  height: 65px;
  background-image: url('../../img/torn2.png');
  position: absolute;
  width: calc(100% - 60px);
  top: 10px;
  left: 30px;
}
div#sign > div::after {
  content: ' ';
  display: block;
  height: 65px;
  background-image: url('../../img/torn1.png');
  position: absolute;
  width: calc(100% - 60px);
  left: 30px;
}
div#sign-base {
  width: 100%;
  height: 70px;
  z-index: 0;
  position: relative;
  margin-top: -30px;
  background-size: 100%;
  background-repeat: no-repeat;
}

@media screen and (max-width: 992px) {
  .ui.container.main .ui.grid .seven.wide.column {
    align-items: baseline;
  }
}
@media screen and (max-width: 768px) {
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 20px !important;
  }
  .left-side-wrapper {
    height: 402px;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 250px;
  }
  div#loader {
    height: 100%;
    align-items: initial;
    padding-top: 240px;
    font-size: 52px;
  }
  div#dialog-box {
    align-items: start;
    padding: 12px;
  }
  .dialog-box-wrap {
    max-height: 100%;
  }
  div#actions-wrapper {
    grid-template-columns: 1fr;
    height: 164px;
  }
}
@media screen and (max-width: 540px) {
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 0px !important;
    height: calc(100vh - 150px);
    align-items: center;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 280px;
  }
  div#dialog-box-container {
    display: flex;
  }
  div#dialog-box p {
    font-size: 14px !important;
    line-height: 20px !important;
  }
  div#actions-wrapper {
    margin-top: 14px;
  }
  div#actions {
    padding-left: 10px;
  }
  div#action-suggestion {
    display: none;
  }
  div#action-suggestion p {
    font-size: 8px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
  }
  .right-side-wrapper {
    width: 100%;
  }
  div#sign {
    min-height: 520px;
    padding: 30px 20px 24px 20px;
  }
  div#sign > div::before,
  div#sign > div::after {
    width: calc(100% - 40px);
    left: 20px;
  }
  h1 {
    font-size: 30px !important;
  }
  h2 {
    font-size: 24px !important;
  }
  h3 {
    font-size: 18px !important;
  }
  p {
    font-size: 8px !important;
  }
}

@media screen and (max-width: 540px) and (min-height: 720px) {
  .ui.container.main .ui.grid .seven.wide.column {
    height: calc(100vh - 220px);
  }
}
@media screen and (max-width: 400px) {
  h1 {
    font-size: 25px !important;
  }
  h2 {
    font-size: 16px !important;
  }
  h3 {
    font-size: 12px !important;
  }
  div#dialog-box p {
    font-size: 10px !important;
    line-height: 16px !important;
  }
}
@media screen and (min-width: 992px) and (max-width: 1198px) {
  div#dialog-box p {
    font-size: 15px !important;
    line-height: 26px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
  }
  .right-side-wrapper {
    width: 100%;
  }
}
@media screen and (min-width: 768px) and (max-width: 992px) {
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 100px !important;
  }
  .left-side-wrapper {
    height: 412px;
  }
  div#avatar-container {
    width: 100px;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 250px;
  }
  img#avatar {
    width: 100px;
    margin-top: 40px;
  }
  div#dialog-box p {
    font-size: 12px !important;
    line-height: 16px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
    padding-left: 20px;
  }
  button.visitor-action::before,
  button.visitor-action:hover::before {
    font-size: 10px;
    left: 6px;
  }
  .right-side-wrapper {
    width: 100%;
  }
}
@media screen and (min-width: 1200px) {
  .ui.container.main {
    margin-top: 60px;
  }
}
@media screen and (min-width: 1200px) and (max-height: 768px) {
  .ui.container.main {
    margin-top: 0px;
  }
  .ui.grid > .column:not(.row) {
    padding: 1rem 0 0 0;
  }
  .seven.wide.column {
    z-index: 1;
    margin: 70px 0 20px;
  }
  div#sign {
    min-height: 580px;
  }
}
